<script>
import Vue from "vue";
import { SET_BREADCRUMB } from "@/core/services/store/breadcrumbs.module";
const VueScrollTo = VueScrollTo;
export default {
  data() {
    return {
       isDisabled:false,
      records: [],
      types: [],
      edit: false,
      url: null,
      record_id: "",
      form: {
        title: null,
        description: null,
        image: null,
        type: null,
      },
      filter: {
        type: null,
        from_date: null,
        to_date: null,
      },
      search: "",
      expanded: [],
      headers: [
        {
          text: "ID",
          align: "start",
          sortable: false,
          value: "id",
        },
        { text: "Type", value: "artical_type.title" },
        { text: "Title", value: "title" },

        { text: "Date", value: "created_at" },
        { text: "Actions", value: "actions", sortable: false },
      ],
    };
  },
  mounted() {
    this.$store.dispatch(SET_BREADCRUMB, [
      { title: "Articles", route: "articles" },
    ]);
  },
  created() {
    this.getRecords();
  },
  methods: {
    onFileChange(e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) {
        console.log("no files");
      }

      const file = files[0];
      // this.url = URL.createObjectURL(file);
      this.form.image = file;
    },
    getValidationState({ dirty, validated, valid = null }) {
      return dirty || validated ? valid : null;
    },
    resetFilter() {
      this.filter = {
        from_date: null,
        to_date: null,
        type: null,
      };
      this.$nextTick(() => {
        this.$refs.observer2.reset();
      });
    },
    resetForm() {
      this.form = {
        title: null,
        description: null,
        image: null,
        type: null,
      };
      this.$nextTick(() => {
        this.$refs.observer.reset();
      });
    },
    getRecords() {
      this.$ApiService
        .get("/Articals")
        .then((response) => {
          this.resetFilter();
          this.records = response.data.data.articals;
          this.types = response.data.data.types;
        })
        .catch((err) => {
          Vue.$toast.error("get records failed");
          console.log(err);
        });
    },

    onSubmit() {
      this.isDisabled=true;

      if (this.edit === false) {
        // add new record
        this.insert();
      } else {
        //update record
        this.update();
      }
    },
    applyFilter() {
      this.$ApiService
        .post("/Articals/Filter", this.filter)
        .then((response) => {
          this.records = response.data.data;
        })
        .catch((err) => {
          Vue.$toast.error("get records failed");
          console.log(err);
        });
    },

    insert() {
      this.$ApiService.setHeader();

      let formData = new FormData();
      for (var key in this.form) {
        formData.append(key, this.form[key]);
      }

      this.$ApiService
        .post("/Artical", formData)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();

            this.$refs.table.scrollIntoView();
            this.getRecords();
            Vue.$toast.success(`${response.data.result}`);
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => {
          Vue.$toast.error("Some error happened, please try again!");
          console.log(err);
        });
        this.isDisabled=false;
    },
    addItem() {
      this.edit = false;
      this.resetForm();
    },
    editItem(item) {
      this.edit = true;
      this.record_id = item.id;
      this.form.title = item.title;
      this.form.description = item.description;
      this.form.type = item.artical_type_id;
    },
    viewImage(item) {
      if (item.url === null) {
        this.$confirm({
          title: "Message",
          message: `There is no Image to be viewed`,
          button: {
            no: "Ok",
          },
        });
      } else window.open(item.url);
    },
    update() {
      let params = new FormData();
      for (var key in this.form) {
        params.append(key, this.form[key]);
      }

      this.$ApiService
        .post(`/Artical/${this.record_id}`, params)
        .then((response) => {
          if (response.data.error == 1) {
            this.resetForm();
            this.edit = false;
            this.record_id = "";

            this.$refs.table.scrollIntoView();
            this.getRecords();
            Vue.$toast.success(`${response.data.result}`);
          } else {
            Vue.$toast.error(`${response.data.result}`);
          }
        })
        .catch((err) => {
          Vue.$toast.error("Some error happened, please try again!");
          console.log(err);
        });
        this.isDisabled=false;
    },
    deleteItem(item) {
      this.$confirm({
        title: "Confirmation",
        message: `Are you sure you want to proceed?`,
        button: {
          no: "No",
          yes: "Yes",
        },
        callback: (confirm) => {
          if (confirm) {
            this.$ApiService
              .delete(`/Artical/${item.id}`)
              .then((response) => {
                if (response.data.error == 1) {
                  this.$refs.table.scrollIntoView();
                  this.getRecords();
                  Vue.$toast.success(`${response.data.result}`);
                } else {
                  Vue.$toast.error(`${response.data.result}`);
                }
              })
              .catch((err) => {
                Vue.$toast.error("Some error happened, please try again!");
                console.log(err);
              });
          }
        },
      });
    },
  }, // end of methods
}; //end of class
</script>
